import Vue from 'vue';
import Vuex from 'vuex';
import { User } from '@/types';
import axios from 'axios';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    user: {} as User,
    countries: [] as any[],
    API_URL: '', // http://localhost:8080
  },
  mutations: {
    setUser: (state, user: User) => {
      state.user = user;
    },
    setCountries: (state, countries: any[]) => {
      state.countries = countries;
    },
  },
  getters: {
    getUser: (state) => {
      return state.user;
    },
    getCountries: (state) => {
      return state.countries;
    },
  },
  actions: {
    loadUser: (context) => {
      if (!context.getters.getUser.id) {
        axios
        .get(context.state.API_URL + '/user')
        .then((response) => {
          const newUser: User = response.data;
          context.commit('setUser', newUser);
        })
        .catch((errorResponse) => {
          console.log(errorResponse);
        });
      }
    },
    loadCountries: (context) => {
      if (!Array.isArray(context.getters.getCountries) || context.getters.getCountries.length === 0) {
        axios
        .get(context.state.API_URL + '/country')
        .then((response) => {
          const countries: any[] = response.data;
          context.commit('setCountries', countries);
        })
        .catch((errorResponse) => {
          console.log(errorResponse);
        });
      }
    },
  },
});
